<template>
	<Plugin :config="config">
		<el-carousel :interval="config.option.interval * 1000"
		             :type="config.option.type || undefined"
		             :trigger="config.option.trigger || undefined"
		             :autoplay="config.option.autoplay"
		             :height="config.style.height + 'px'">
			<el-carousel-item v-for="(item, i) in config.option.data" :key="i">
				<div class="image" :style="calcImage(item)"></div>
			</el-carousel-item>
		</el-carousel>
	</Plugin>
</template>

<script>
	import '../../../../assets/styles/common/paint/slider.scss'
	import Plugin from '@/components/screen/layer/base/Plugin'
	import _ from 'lodash'

	export default {
		name: 'Slider',
		props: ['config'],
		data() {
			return {
				// 当前选中的按钮索引
				currentIndex: 0
			}
		},
		created() {
			this.initStyle()
			this.initData()
			this.initOption()
			this.initEvent()
			this.initProxy()
			this.initTransform()
		},
		methods: {
			/**
			 * 计算按钮样式
			 */
			calcImage(item) {
				return {backgroundImage: 'url(' + item.url + ')' || '',}
			},
			/**
			 * 初始化样式
			 */
			initStyle() {
				let style = _.assignIn({}, {
					width: 320,
					height: 200,
				}, this.config.style)
				this.config.style = style
			},
			/**
			 * 声明默认静态数据
			 */
			initData() {
				this.config.data = null
			},
			/**
			 * 声明默认option
			 */
			initOption() {
				this.config.option = this.config.option || {
					interval: 4,
					type: undefined,
					trigger: undefined,
					autoplay: true,
					data: [
						{name: '1.jpg', url: './img/sample/slider/1.jpg'},
						{name: '2.jpg', url: './img/sample/slider/2.jpg'},
						{name: '3.jpg', url: './img/sample/slider/3.jpg'},
					]
				}
			},
			/**
			 * 声明默认的事件，以及事件触发时的参数中，对外提供配置，从而绑定全局状态的属性
			 * 例如，chart事件的参数是一整个param，里面包含name，value等属性，
			 * 此时声明 binder 为 {name: 'name', comment: '数据名', prop: 'name', state: null} 表示：
			 * name: 面板上展示的属性名
			 * comment: 面板上展示的属性说明文字
			 * prop: 该属性实际对应的param的属性路径，将来会根据该路径取值，return _.get(param, prop)
			 * state: 将该属性绑定到全局状态中的哪个属性，
			 * 全局状态存储位置为 this.$store.state.screen.design.interaction[state]
			 */
			initEvent() {
				this.config.event = null
			},
			/**
			 * 初始化数据代理配置，默认静态数据
			 */
			initProxy() {
				this.config.proxy = null
			},
			/**
			 * 初始化数据属性映射规则
			 */
			initTransform() {
				this.config.transform = null
			},
			/**
			 * 将config.data按照规则转换为option中的展示数据的过程
			 */
			handler() {
			},
		},
		components: {
			'Plugin': Plugin
		},
	}
</script>
