<template>
	<Map :config="config"></Map>
</template>

<script>
	import Map from '@/components/screen/layer/base/Map'
	import 'echarts-gl'
	import API from '../../../../config/api'
	import _ from 'lodash'

	export default {
		name: 'ThreeDimBarMap',
		props: ['config'],
		data() {
			return {}
		},
		methods: {
			/**
			 * 声明默认配置项
			 */
			initOption() {
				let defaultOption = {
					backgroundColor: 'rgba(0,0,0,0)',
					geo3D: {
						map: 'china',
						shading: 'lambert',
						environment: null,
						boxWidth: 50,
						emphasis: {
							label: {show: false}
						},
						light: {
							main: {
								intensity: 5,
								shadow: true,
								shadowQuality: 'high',
								alpha: 30,
							},
							ambient: {
								intensity: 0
							},
							ambientCubemap: {
								texture: API.RESOURCES.HDR_RESOURCE,
								exposure: 1,
								diffuseIntensity: 0.5
							}
						},
						viewControl: {
							distance: 50,
							panMouseButton: 'left',
							rotateMouseButton: 'right'
						},
						groundPlane: {
							show: true,
							color: 'rgba(0,0,0,0)'
						},
						postEffect: {
							enable: true,
							bloom: {
								enable: false
							},
							SSAO: {
								radius: 1,
								intensity: 1,
								enable: true
							},
							depthOfField: {
								enable: false,
								focalRange: 10,
								blurRadius: 10,
								fstop: 1
							}
						},
						temporalSuperSampling: {
							enable: true
						},
						itemStyle: {},
						regionHeight: 2
					},
					visualMap: {
						show: true,
						min: 0,
						max: 40,
						itemWidth: 20,
						itemHeight: 100,
						left: 'left',
						orient: 'vertical',
						calculable: true,
						realtime: false,
						inRange: {
							color: ['#313695', '#4575b4', '#74add1', '#abd9e9', '#e0f3f8', '#ffffbf', '#fee090', '#fdae61', '#f46d43', '#d73027', '#a50026']
						},
						outOfRange: {
							colorAlpha: 0
						}
					},
					series: [{
						type: 'bar3D',
						coordinateSystem: 'geo3D',
						shading: 'lambert',
						data: null,
						barSize: 0.2,
						minHeight: 0.1,
						silent: true,
						itemStyle: {
							color: 'orange'
						}
					}]
				}
				this.config.option = _.assignIn(defaultOption, this.config.option)
			},
			/**
			 * 声明默认静态数据
			 */
			initData() {
				this.config.data = this.config.data || null
			},
			/**
			 * 声明默认的事件，以及事件触发时的参数中，对外提供配置，从而绑定全局状态的属性
			 * 例如，chart事件的参数是一整个param，里面包含name，value等属性，
			 * 此时声明 binder 为 {name: 'name', comment: '数据名', prop: 'name', state: null} 表示：
			 * name: 面板上展示的属性名
			 * comment: 面板上展示的属性说明文字
			 * prop: 该属性实际对应的param的属性路径，将来会根据该路径取值，return _.get(param, prop)
			 * state: 将该属性绑定到全局状态中的哪个属性，
			 * 全局状态存储位置为 this.$store.state.screen.design.interaction[state]
			 */
			initEvent() {
				this.config.event = this.config.event || []
			},
			/**
			 * 初始化数据代理配置，默认静态数据
			 */
			initProxy() {
				this.config.proxy = this.config.proxy || {
					type: 0,
					url: API.SAMPLE.THREE_DIM_BAR_MAP,
				}
			},
			/**
			 * 初始化数据属性映射规则
			 */
			initTransform() {
				this.config.transform = this.config.transform || {
					mapper: {
						x: null,
						y: null,
						value: null
					},
				}
			},
			/**
			 * 将config.data按照规则转换为option中的展示数据的过程
			 */
			handler() {
				if (!this.config.data || !this.config.data.length) {
					return
				}
				let data = []
				let propX = this.config.transform.mapper.x || 'x'
				let propY = this.config.transform.mapper.y || 'y'
				let propValue = this.config.transform.mapper.value || 'value'
				for (let i = 0; i < this.config.data.length; i++) {
					let item = this.config.data[i]
					let x = item[propX]
					let y = item[propY]
					let value = item[propValue]
					data.push([x, y, value])
				}
				this.config.option.series[0].data = data
			}
		},
		components: {
			'Map': Map
		},
	}
</script>
