<template>
	<el-collapse value="0" accordion>
		<!-- 基础属性 -->
		<el-collapse-item title="基础属性" name="0">
			<el-row>
				<el-col :span="8" class="tag">尺寸</el-col>
				<el-col :span="8">
					<el-input-number v-model="component.config.style.width"
					                 size="mini"
					                 controls-position="right"
					                 :min="0">
					</el-input-number>
					<div class="description">宽度</div>
				</el-col>
				<el-col :span="8">
					<el-input-number v-model="component.config.style.height"
					                 size="mini"
					                 controls-position="right"
					                 :min="0">
					</el-input-number>
					<div class="description">高度</div>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="8" class="tag">位置</el-col>
				<el-col :span="8">
					<el-input-number v-model="component.config.style.left"
					                 size="mini"
					                 controls-position="right">
					</el-input-number>
					<div class="description">横坐标</div>
				</el-col>
				<el-col :span="8">
					<el-input-number v-model="component.config.style.top"
					                 size="mini"
					                 controls-position="right">
					</el-input-number>
					<div class="description">纵坐标</div>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="8" class="tag">其他</el-col>
				<el-col :span="8">
					<el-input-number v-model="component.config.style.opacity"
					                 size="mini"
					                 controls-position="right" :min="0" :max="1" :step="0.05">
					</el-input-number>
					<div class="description">透明度</div>
				</el-col>
				<el-col :span="8">
				</el-col>
			</el-row>
		</el-collapse-item>
		<!-- 样式 -->
		<el-collapse-item title="样式" name="1">
			<el-row>
				<el-col :span="8" class="tag">背景色</el-col>
				<el-col :span="8">
					<el-color-picker v-model="component.config.style.backgroundColor"
					                 size="mini" show-alpha></el-color-picker>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="8" class="tag">背景图</el-col>
				<el-col :span="16">
					<el-input v-model="component.config.style.backgroundImage"
					          size="mini" clearable>
					</el-input>
					<el-upload drag
					           class="img-upload"
					           :headers="getToken()"
					           :action="getUploadUrl()"
					           :before-upload="beforeUpload"
					           :on-success="onSuccessCover"
					           :on-Error="onError"
					           :show-file-list="false">
						<img v-if="component.config.style.backgroundImage"
						     :src="component.config.style.backgroundImage"
						     class="img-preview">
						<i class="el-icon-upload"></i>
						<div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
					</el-upload>
					<div v-if="component.config.style.backgroundImage"
					     class="img-cover">
						<i class="icon iconfont icon-delete" @click="removeCoverImage()"></i>
					</div>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="8" class="tag">角</el-col>
				<el-col :span="8">
					<el-input-number v-model="component.config.style.borderTopLeftRadius"
					                 size="mini"
					                 controls-position="right"
					                 :min="0">
					</el-input-number>
					<div class="description">左上</div>
					<el-input-number v-model="component.config.style.borderBottomLeftRadius"
					                 size="mini"
					                 controls-position="right"
					                 :min="0">
					</el-input-number>
					<div class="description">左下</div>
				</el-col>
				<el-col :span="8">
					<el-input-number v-model="component.config.style.borderTopRightRadius"
					                 size="mini"
					                 controls-position="right"
					                 :min="0">
					</el-input-number>
					<div class="description">右上</div>
					<el-input-number v-model="component.config.style.borderBottomRightRadius"
					                 size="mini"
					                 controls-position="right"
					                 :min="0">
					</el-input-number>
					<div class="description">右下</div>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="8" class="tag">边框</el-col>
				<el-col :span="8">
					<el-select v-model="component.config.style.borderStyle"
					           placeholder="请选择"
					           size="mini">
						<el-option v-for="item in borderStyle"
						           :key="item.value"
						           :label="item.label"
						           :value="item.value">
						</el-option>
					</el-select>
					<div class="description">线型</div>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="8" :offset="8">
					<el-input-number v-model="component.config.style.borderWidth"
					                 size="mini"
					                 controls-position="right"
					                 :min="0">
					</el-input-number>
					<div class="description">线宽</div>
				</el-col>
				<el-col :span="8">
					<el-color-picker v-model="component.config.style.borderColor"
					                 size="mini" show-alpha></el-color-picker>
				</el-col>
				<div class="description">颜色</div>
			</el-row>
		</el-collapse-item>
	</el-collapse>
</template>

<script>
	import API from '../../../../config/api'

	export default {
		name: 'Label',
		props: ['component'],
		data() {
			return {
				borderStyle: [
					{label: '无', value: 'none'},
					{label: '实线', value: 'solid'},
					{label: '点状', value: 'dotted'},
					{label: '虚线', value: 'dashed'},
					{label: '双实线', value: 'double'},
					{label: '3D凹槽', value: 'groove'},
					{label: '3D垄状', value: 'ridge'},
					{label: '3D内嵌', value: 'inset'},
					{label: '3D外凸', value: 'outset'},
				]
			}
		},
		created() {
		},
		computed: {},
		methods: {
			/**
			 * 获取token作文参数上传
			 */
			getToken() {
				return {token: this.$store.state.token}
			},
			/**
			 * 上传之前
			 */
			beforeUpload(file) {
				let name = file.name
				let ext = name.substring(name.lastIndexOf(".") + 1).toLowerCase()
				if (ext !== "jpg" && ext !== "png" && ext !== 'bmp' && ext !== 'jpeg') {
					this.$message({
						message: "只支持jpg、png、bmp文件",
						type: "error"
					})
					return false
				}
			},
			/**
			 * 获取上传地址
			 */
			getUploadUrl() {
				return API.ATTACHMENT.UPLOAD
			},
			/**
			 * 上传成功
			 */
			onSuccessCover(res) {
				this.component.config.style.backgroundImage = API.ATTACHMENT.DOWNLOAD + "?id=" + res.data.id
			},
			/**
			 * 上传失败
			 */
			onError(err) {
				this.$message({
					message: err.data.message,
					type: "error"
				})
			},
			/**
			 * 移除图
			 */
			removeCoverImage() {
				this.component.config.style.backgroundImage = null
			}
		},
		components: {},
	}
</script>
