<template>
	<Map :config="config"></Map>
</template>

<script>
	import Map from '@/components/screen/layer/base/Map'
	import ChinaCity from '../../../map/coordinate/china-city'
	import _ from 'lodash'

	export default {
		name: 'ColorMap',
		props: ['config'],
		data() {
			return {}
		},
		methods: {
			/**
			 * 声明默认配置项
			 */
			initOption() {
				let defaultOption = {
					backgroundColor: 'transparent',
					visualMap: {
						show: true,
						orient: 'vertical',
						min: 0,
						max: 800,
						calculable: true,
						color: ['#4FA8F9', '#FFFFFF'],
						left: 'left',
						itemWidth: 20,
						itemHeight: 100,
						textStyle: {
							color: '#fff'
						}
					},
					series: [{
						type: 'map',
						mapType: 'china',
						zlevel: 2,
						data: null
					}]
				}
				this.config.option = _.assignIn(defaultOption, this.config.option)
			},
			/**
			 * 声明默认静态数据
			 */
			initData() {
				this.config.data = this.config.data || [
					{name: "北京", value: 852},
					{name: "天津", value: 574},
					{name: "上海", value: 682},
					{name: "重庆", value: 432},
					{name: "河北", value: 188},
					{name: "河南", value: 315},
					{name: "云南", value: 596},
					{name: "辽宁", value: 494},
					{name: "黑龙江", value: 4},
					{name: "湖南", value: 539},
					{name: "安徽", value: 342},
					{name: "山东", value: 514},
					{name: "新疆", value: 366},
					{name: "江苏", value: 257},
					{name: "浙江", value: 740},
					{name: "江西", value: 350},
					{name: "湖北", value: 278},
					{name: "广西", value: 557},
					{name: "甘肃", value: 417},
					{name: "山西", value: 363},
					{name: "内蒙古", value: 175},
					{name: "陕西", value: 927},
					{name: "吉林", value: 431},
					{name: "福建", value: 387},
					{name: "贵州", value: 351},
					{name: "广东", value: 157},
					{name: "青海", value: 609},
					{name: "西藏", value: 819},
					{name: "四川", value: 791},
					{name: "宁夏", value: 346},
					{name: "海南", value: 462},
					{name: "台湾", value: 367},
					{name: "香港", value: 533},
					{name: "澳门", value: 935}
				]
			},
			/**
			 * 声明默认的事件，以及事件触发时的参数中，对外提供配置，从而绑定全局状态的属性
			 * 例如，chart事件的参数是一整个param，里面包含name，value等属性，
			 * 此时声明 binder 为 {name: 'name', comment: '数据名', prop: 'name', state: null} 表示：
			 * name: 面板上展示的属性名
			 * comment: 面板上展示的属性说明文字
			 * prop: 该属性实际对应的param的属性路径，将来会根据该路径取值，return _.get(param, prop)
			 * state: 将该属性绑定到全局状态中的哪个属性，
			 * 全局状态存储位置为 this.$store.state.screen.design.interaction[state]
			 */
			initEvent() {
				this.config.event = this.config.event || [{
					name: 'click',
					comment: '点击底图事件',
					filter: function (params) {
						if (params.componentType === "geo") {
							return true
						} else {
							return false
						}
					},
					binders: [
						{name: 'name', comment: '行政区划名', prop: 'name', state: null},
					]
				}]
			},
			/**
			 * 初始化数据代理配置，默认静态数据
			 */
			initProxy() {
				this.config.proxy = this.config.proxy || {
					type: 1,
				}
			},
			/**
			 * 初始化数据属性映射规则
			 */
			initTransform() {
				this.config.transform = this.config.transform || {
					mapper: {
						name: null,
						value: null
					},
				}
			},
			/**
			 * 将config.data按照规则转换为option中的展示数据的过程
			 */
			handler() {
				let data = []
				let propName = this.config.transform.mapper.name || 'name'
				let propValue = this.config.transform.mapper.value || 'value'
				for (let i = 0; i < this.config.data.length; i++) {
					let item = this.config.data[i]
					let name = item[propName]
					let value = item[propValue]
					data.push({
						name: name,
						value: value
					})
				}
				this.config.option.series[0].data = data
			}
		},
		components: {
			'Map': Map
		},
	}
</script>
