<template>
	<el-collapse value="0" accordion>
		<!-- 基础属性 -->
		<el-collapse-item title="基础属性" name="0">
			<el-row>
				<el-col :span="8" class="tag">尺寸</el-col>
				<el-col :span="8">
					<el-input-number v-model="component.config.style.width"
					                 size="mini"
					                 controls-position="right"
					                 :min="0">
					</el-input-number>
					<div class="description">宽度</div>
				</el-col>
				<el-col :span="8">
					<el-input-number v-model="component.config.style.height"
					                 size="mini"
					                 controls-position="right"
					                 :min="0">
					</el-input-number>
					<div class="description">高度</div>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="8" class="tag">位置</el-col>
				<el-col :span="8">
					<el-input-number v-model="component.config.style.left"
					                 size="mini"
					                 controls-position="right">
					</el-input-number>
					<div class="description">横坐标</div>
				</el-col>
				<el-col :span="8">
					<el-input-number v-model="component.config.style.top"
					                 size="mini"
					                 controls-position="right">
					</el-input-number>
					<div class="description">纵坐标</div>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="8" class="tag">其他</el-col>
				<el-col :span="8">
					<el-input-number v-model="component.config.style.opacity"
					                 size="mini"
					                 controls-position="right" :min="0" :max="1" :step="0.05">
					</el-input-number>
					<div class="description">透明度</div>
				</el-col>
				<el-col :span="8">
				</el-col>
			</el-row>
		</el-collapse-item>
		<!-- 样式 -->
		<el-collapse-item title="样式" name="1">
			<el-row>
				<el-col :span="8" class="tag">间隔时间</el-col>
				<el-col :span="8">
					<el-input-number v-model="component.config.option.interval"
					                 size="mini"
					                 controls-position="right"
					                 :min="0">
					</el-input-number>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="8" class="tag">自动播放</el-col>
				<el-col :span="8">
					<el-checkbox v-model="component.config.option.autoplay"></el-checkbox>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="8" class="tag">样式</el-col>
				<el-col :span="8">
					<el-select v-model="component.config.option.type"
					           placeholder="请选择"
					           size="mini">
						<el-option v-for="item in type"
						           :key="item.value"
						           :label="item.label"
						           :value="item.value">
						</el-option>
					</el-select>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="8" class="tag">切换方式</el-col>
				<el-col :span="8">
					<el-select v-model="component.config.option.trigger"
					           placeholder="请选择"
					           size="mini">
						<el-option v-for="item in trigger"
						           :key="item.value"
						           :label="item.label"
						           :value="item.value">
						</el-option>
					</el-select>
				</el-col>
			</el-row>
		</el-collapse-item>
		<!-- 图片 -->
		<el-collapse-item title="图片" name="2">
			<template v-for="(item, i) in component.config.option.data">
				<el-row>
					<el-col :span="8" class="tag">{{'图片' + i}}</el-col>
					<el-col :span="16">
						<el-input v-model="component.config.option.data[i].url"
						          placeholder="请输入图片网址"
						          size="mini" clearable></el-input>
					</el-col>
				</el-row>
			</template>
			<el-row>
				<el-col :span="16" :offset="8">
					<el-upload drag
					           class="img-upload"
					           :file-list="component.config.option.data"
					           :headers="getToken()"
					           :action="getUploadUrl()"
					           :before-upload="beforeUpload"
					           :on-success="onSuccessCover"
					           :on-Error="onError"
					           :on-remove="onRemove"
					           :show-file-list="true"
					           multiple>
						<i class="el-icon-upload"></i>
						<div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
					</el-upload>
				</el-col>
			</el-row>
		</el-collapse-item>
	</el-collapse>
</template>

<script>
	import API from '../../../../config/api'

	export default {
		name: 'Slider',
		props: ['component'],
		data() {
			return {
				type: [
					{label: '原始', value: undefined},
					{label: '卡片', value: 'card'},
				],
				trigger: [
					{label: '悬浮', value: undefined},
					{label: '点击', value: 'click'},
				]
			}
		},
		methods: {
			/**
			 * 获取token作文参数上传
			 */
			getToken() {
				return {token: this.$store.state.token}
			},
			/**
			 * 上传之前
			 */
			beforeUpload(file) {
				let name = file.name
				let ext = name.substring(name.lastIndexOf(".") + 1).toLowerCase()
				if (ext !== "jpg" && ext !== "png" && ext !== 'bmp' && ext !== 'jpeg') {
					this.$message({
						message: "只支持jpg、png、bmp文件",
						type: "error"
					})
					return false
				}
			},
			/**
			 * 获取上传地址
			 */
			getUploadUrl() {
				return API.ATTACHMENT.UPLOAD
			},
			/**
			 * 上传成功
			 */
			onSuccessCover(response, file, fileList) {
				this.component.config.option.data.push({
					name: file.name,
					url: API.ATTACHMENT.DOWNLOAD + "?id=" + response.data.id
				})
			},
			/**
			 * 上传失败
			 */
			onError(err) {
				this.$message({
					message: err.data.message,
					type: "error"
				})
			},
			/**
			 * 移除图
			 */
			onRemove(file, fileList) {
				let data = []
				if (fileList && fileList.length) {
					for (let i = 0; i < fileList.length; i++) {
						let item = fileList[i]
						data.push({
							name: item.name,
							url: item.url
						})
					}
				}
				this.component.config.option.data = data
			}
		}
	}
</script>
